.about-hero {
  background: url("../img/about-hero-bg.webp") no-repeat top right/ contain,
    url("../img/about-hero-bg.png") no-repeat top right / contain;

  @media (max-width: 480px) {
    background: url("../img/about-hero-bg_mobile.webp") no-repeat top center/
        contain,
      url("../img/about-hero-bg_mobile.png") no-repeat top center / contain;
  }

  &__content {
    @media (max-width: 480px) {
      max-width: 408px;
    }
    &-top {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 30px;
      @media (max-width: 480px) {
        justify-content: center;
      }
    }

    &-text {
      flex: 1 1 755px;
      font-size: 24px;
      line-height: 29px;
      color: var(--dark-color);

      &:not(:first-child) {
        margin-top: 30px;
        font-weight: 300;
      }

      @media (max-width: 480px) {
        font-size: 20px;
      }
    }

    &-cta {
      display: flex;
      flex-direction: column;
      gap: 13px;

      @media (max-width: 1024px) {
        flex-direction: row;
        justify-content: center;
        width: 100%;

        .btn-primary {
          display: none;
        }
      }

        @media (max-width: 450px) {
          flex-direction: column;
          align-items: flex-start;
        }
      }
    }

  &__form {
    margin-top: 50px;
  }
}
