.audit-product {
  background: var(--dark-color);
  background-repeat: no-repeat;
  background-position: center;
  color: var(--light-color);

  background-image: url("../img/audit-products-bg.webp");

  @supports not (background-image: url("../img/audit-products-bg.webp")) {
    background-image: url("../img/audit-products-bg.png");
  }

  &__content {
    @media (max-width: 480px) {
      max-width: 408px;
    }
  }

  &__title {
    margin-bottom: 70px;
  }

  &__text {
    font-family: "Montserrat";
    font-weight: 400;
    font-size: 24px;
    line-height: normal;
    margin-bottom: 70px;

    @media (max-width: 480px) {
      font-size: 20px;
    }
  }

  &__subtitle {
    font-family: "RubikMonoOne";
    font-weight: 400;
    font-size: 30px;
    line-height: normal;
    margin-bottom: 70px;

    @media (max-width: 480px) {
      font-size: 26px;
    }
  }

  &__list {
    padding: 0 0 0 30px;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 24px;
    margin-bottom: 70px;

    @media (max-width: 480px) {
      gap: 20px;
    }
  }

  &__item {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: normal;

    @media (max-width: 480px) {
      font-size: 20px;
    }
  }
}

.audit-product__title > span {
  color: #cdffcc;
}

.audit-product__text > span {
  color: #cdffcc;
}

.audit-product__item > span {
  font-weight: 500;
}
