.services-list {
  &__content {
    @media (max-width: 480px) {
      max-width: 408px;
    }
    &-title {
      max-width: 560px;
      font-family: "RubikMonoOne";
      font-style: normal;
      font-weight: 400;
      font-size: 38px;
      line-height: 47px;
      color: var(--dark-color);
    }

    &-list {
      margin-top: 70px;
      display: flex;
      justify-content: center;
      gap: 36px;
      flex-wrap: wrap;
    }
  }
}
