@charset "UTF-8";
:root {
  --font-family: "Montserrat", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --primary-accent-color: #cdffcc;
  --secondary-accent-color: #896e4e;
  --dark-color: #000;
}

